export const ErrorCode = {
    "DUPLICATE_KEY_CODE": 11000,
};


export class ErrorUtil {
    constructor() {
        this.errorMessage = [];
    }

    addValidationError = (validationError, originalError) => {
        originalError = originalError || {};

        if (validationError) {
            Object.assign(originalError, {validationError: {...validationError}});
        }

        this.addError(originalError, validationError);

    };

    addError = (error) => {
        error = Object.assign({}, error);
        error.message = error.message || 'Unknown error. Please contact the software vendor.';
        this.errorMessage.push(error);
    };

    getErrorsAsString = () => {
        return JSON.stringify(this.errorMessage);
    };


}




